import { useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { StyledHeader, StyledSubheader, StyledSubheader3 } from '../styled';
import { majorFactions, minorFactions } from './factionData';
import './factions.css'

function Faction(props) {
  const {id, name, subhead, src, description} = props.faction;
  const history = useHistory();
  function goToFaction(id) {
    if (id) {
      history.push(`/factions/${id}`);
    }
  }

  return (
    <section
      className={`faction-section ${id ? 'faction-record' : ''}`}
      onClick={() => goToFaction(id)}
    >
      <aside>
        <img src={src} alt={name}></img>
      </aside>
      <article>
        <StyledSubheader>{name}</StyledSubheader>
        <StyledSubheader3>{subhead}</StyledSubheader3>
        <p>{description}</p>
      </article>
    </section>
  );
}

function Factions() {
  const majorRef = useRef(null);
  const minorRef = useRef(null);

  const barHeight = '50px';
  function scrollRefWithOffset(scrollRef) {
    scrollRef.current.style.scrollMargin = barHeight;
    scrollRef.current.scrollIntoView({ behavior: 'smooth' });
  }

  return (
    <>
      <nav className="sub-nav">
        <ul className="navigation">
          <li onClick={() => scrollRefWithOffset(majorRef)}>Major Factions</li>
          <li onClick={() => scrollRefWithOffset(minorRef)}>Minor Factions</li>
        </ul>
      </nav>
      <StyledHeader ref={majorRef}>Major Factions</StyledHeader>
      <ul>
        {majorFactions.map(function (faction, index) {
          return (
            <li key={`fact-major-${index}`}>
              <Faction faction={faction} />
            </li>
          );
        })}
      </ul>

      <StyledHeader style={{paddingTop: '1rem'}} ref={minorRef}>Minor Factions</StyledHeader>
      <ul>
        {minorFactions.map(function (faction, index) {
          return (
            <li key={`fact-minor-${index}`}>
              <Faction faction={faction} />
            </li>
          );
        })}
      </ul>
    </>
  );
}

export default Factions;