import { useState } from 'react';
import ImageModal from '../ImageModal';
import SectionParagraph from '../shared/SectionParagraph';
import allEvents from './eventPageData/index';
import {
  description,
  gallery,
  headline,
  quotation,
  thumbnail,
  defaultGalleryWidth,
  defaultThumbWidth,
  right
} from './eventPageData/constants';
import {
  StyledHeader, StyledSubheader, StyledSubheader3, StyledSubheader4, StyledParagraph
} from '../styled';
import './events.css';
import '../shared/shared.css';

import exterminatus from '../../assets/exterminatus.jpeg';

function EventParagraph({ paragraph, openModal }) {
  const { type } = paragraph;
  switch(type) {
    case description:
      return (
        <p>{paragraph.text}</p>
      );
    case headline:
      return (
        <StyledSubheader3>{paragraph.text}</StyledSubheader3>
      );
    case quotation:
      return (
        <aside className="event-quote-card">
          <div className="event-quote">
            {paragraph.text.map(function(line, i) {
              return (
                <StyledParagraph key={`quote-${i}`}>{line}</StyledParagraph>
              )
            })}
          </div>
          {paragraph.speaker && <StyledParagraph>&#8212; {paragraph.speaker}</StyledParagraph>}
        </aside>
      );
    case thumbnail:
      const { caption, image } = paragraph;
      const align = paragraph.align || right;
      const widthFactor = paragraph.widthFactor || 1.0;
      let width = Math.round((defaultThumbWidth * widthFactor) / 10) * 10;
      if (width < 100) { width = 100; }
      if (width > 500) { width = 500; }
      return (
        <div className={`thumb t${align}`}>
          <div className="thumbinner" style={{width: width + 'px'}}>
            <a className="image" role="button" onClick={() => openModal(image, caption)}>
              <img className="thumbimage" src={image} />
            </a>
            <div className="thumbcaption">
              {caption}
            </div>
          </div>
        </div>
      );
    case gallery:
      return (
        <ul className="gallery">
          {paragraph.galleryboxes.map(function(item, i) {
            const { image, caption } = item;
            // const widthFactor = paragraph.widthFactor || 1.0;
            // const heightFactor = paragraph.heightFactor || 1.0;
            // let width = Math.round((defaultGalleryWidth * widthFactor) / 10) * 10;
            // if (width < 100) { width = 100; }
            // if (width > 500) { width = 500; }
            return (
              <li className="gallerybox" key={`gb-${i}`}>
                <div className="thumb">
                  <div className="gallery-img-wrapper">
                    <a
                      className="image"
                      role="button"
                      onClick={() => openModal(image, caption)}
                    >
                      <img className="thumbimage" src={image} />
                    </a>
                  </div>
                </div>
                <div className="gallerytext">
                  <p>{caption}</p>
                </div>
              </li>
            )
          })}
        </ul>
      );
    default:
      return null;
  }
}

function EventPage(props) {
  const [imageInModal, setImageInModal] = useState(null);
  function closeModal() {
    setImageInModal(null);
  }
  function openModal(src, alt) {
    setImageInModal({src, alt});
  }

  const { eventId } = props.match.params;
  const event = allEvents[eventId];
  if (!event) {
    return (
      <article>
        <StyledHeader>All records purged</StyledHeader>
        <img src={exterminatus} alt="" />
      </article>
    );
  }

  const { title, date, attacker, defender, gameType, titleCard, summary } = event;

  return (
    <article className="event-header">
      {imageInModal ? <ImageModal closeModal={closeModal} image={imageInModal} /> : null}

      <div className="event-header">
        <StyledHeader>{title}</StyledHeader>
        <StyledSubheader3>{attacker.faction} vs. {defender.faction}, {date}</StyledSubheader3>
        <StyledSubheader4>{gameType}</StyledSubheader4>
        <div className="thumb tnone">
          <div className="thumbinner" alt={title}>
            <a className="image" role="button" onClick={() => openModal(titleCard, title)}>
              <img className="thumbimage event-main" src={titleCard} alt={title} />
            </a>
          </div>
        </div>

        <section className="army-lists">
          <aside className="army-list attacker">
            <StyledSubheader>{attacker.faction}</StyledSubheader>
            <StyledSubheader3>{attacker.general}</StyledSubheader3>
            {attacker.picture &&
              <div className="thumb tnone">
                <div className="thumbinner" alt={attacker.general}>
                  <a
                    className="image"
                    role="button"
                    onClick={() => openModal(attacker.picture, attacker.general)}
                  >
                    <img className="thumbimage event-faction" src={attacker.picture} />
                  </a>
                </div>
              </div>
            }
            {attacker.armyList?.map(function(unit, i) {
              return <StyledParagraph key={`unit-${i}`}>{unit}</StyledParagraph>
            })}
          </aside>
          <aside className="army-list defender">
            <StyledSubheader>{defender.faction}</StyledSubheader>
            <StyledSubheader3>{defender.general}</StyledSubheader3>
            {defender.picture &&
              <div className="thumb tnone">
                <div className="thumbinner" alt={defender.general}>
                  <a
                    className="image"
                    role="button"
                    onClick={() => openModal(defender.picture, defender.general)}
                  >
                    <img className="thumbimage event-faction" src={defender.picture} />
                  </a>
                </div>
              </div>
            }
            {defender.armyList?.map(function(unit, i) {
              return <StyledParagraph key={`unit-${i}`}>{unit}</StyledParagraph>
            })}
          </aside>
        </section>
      </div>

      <section className="event-summary">
        {summary.map(function(paragraph, index) {
          return (
            <SectionParagraph
              key={`paragraph-${index}`}
              paragraph={paragraph}
              openModal={openModal}
            />
          );
        })}
      </section>
    </article>
  );
}

export default EventPage;