import { useState } from 'react';
import ImageModal from '../ImageModal';
import SectionParagraph from '../shared/SectionParagraph';
import allFactions from './factionPageData/index';
import {
  StyledHeader, StyledSubheader, StyledSubheader3, StyledSubheader4, StyledParagraph
} from '../styled';
import './factions.css';
import '../shared/shared.css'

import exterminatus from '../../assets/exterminatus.jpeg';

function FactionPage(props) {
  const [imageInModal, setImageInModal] = useState(null);
  function closeModal() {
    setImageInModal(null);
  }
  function openModal(src, alt) {
    setImageInModal({ src, alt });
  }

  const { factionId } = props.match.params;
  const faction = allFactions[factionId];
  if (!faction) {
    return (
      <article>
        <StyledHeader>All records purged</StyledHeader>
        <img src={exterminatus} alt="" />
      </article>
    );
  }

  const { title, subhead, symbol, headerImg, summary, orkify } = faction;

  return (
    <div className="faction-page">
      {imageInModal ? <ImageModal closeModal={closeModal} image={imageInModal} /> : null}

      <section className="faction-header">
        {headerImg && <a className="image" role="button" onClick={() => openModal(headerImg, title)}>
          <img className="thumbimage faction-main" src={headerImg} alt={title} />
        </a>}
        <div className="faction-headers">
          <StyledHeader className={orkify ? "orkish" : ""}>{title}</StyledHeader>
          <StyledSubheader className={orkify ? "ork-heading" : ""}>{subhead}</StyledSubheader>
        </div>
      </section>

      <section className="faction-summary">
        {summary.map(function (paragraph, index) {
          return (
            <SectionParagraph
              key={`paragraph-${index}`}
              paragraph={paragraph}
              openModal={openModal}
            />
          );
        })}
      </section>

      <img src={symbol} className="end-symbol" alt="" />
    </div>
  );
}

export default FactionPage;