import { useState } from 'react';
import { NavLink, Switch, Route } from 'react-router-dom';

import Home from './Home';
import Background from './Background';
import Maps from './maps/Maps';
import Events from './events/Events';
import EventPage from './events/EventPage';
import Factions from './factions/Factions';
import FactionPage from './factions/FactionPage';
import Characters from './characters/Characters';

import '../styles/App.css';

function App() {
  const [menuOpen, setMenuOpen] = useState(false);
  function toggleMenuOpen() {
    return setMenuOpen(!menuOpen);
  }
  function closeMenu() {
    return setMenuOpen(false);
  }

  return (
    <div className="App">

      <header className="header">
        <input
          id="side-menu"
          className="side-menu"
          type="checkbox"
          checked={menuOpen}
          onChange={toggleMenuOpen}
        />
        <label className="hamb" htmlFor="side-menu">
          <span className="hamb-line"></span>
        </label>

        <nav className="menu-nav">
          <ul className="font-face-ca navigation">
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/" exact>Home</NavLink>
            </li>
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/background">Background</NavLink>
            </li>
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/maps">Maps</NavLink>
            </li>
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/events">Events</NavLink>
            </li>
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/factions">Factions</NavLink>
            </li>
            <li onClick={closeMenu}>
              <NavLink activeClassName="menu-active" to="/characters">Characters</NavLink>
            </li>
          </ul>
        </nav>
      </header>

      <Route path="/maps">
        <div id="maps-background">
          <Maps />
        </div>
      </Route>

      <Switch>
        <Route exact path="/">
          <div id="banner-image" className="home"></div>
          <article>
            <Home />
          </article>
        </Route>

        <Route path="/background">
          <div id="banner-image" className="background"></div>
          <article>
            <Background />
          </article>
        </Route>

        <Route exact path="/events">
          <div id="banner-image" className="events"></div>
          <article>
            <Events />
          </article>
        </Route>

        <Route path="/events/:eventId" component={EventPage} />

        <Route exact path="/factions">
          <div id="banner-image" className="factions"></div>
          <article>
            <Factions />
          </article>
        </Route>

        <Route path="/factions/:factionId" component={FactionPage} />

        <Route path="/characters">
          <div id="banner-image" className="characters"></div>
          <article>
            <Characters />
          </article>
        </Route>
      </Switch>
    </div>
  );
}

export default App;
