export function filtersReducer(filters, action) {
  switch (action.type) {
    case 'addFaction': {
      filters.factions.add(action.filter);
      return Object.assign({}, filters);
    }
    case 'removeFaction': {
      filters.factions.delete(action.filter);
      return Object.assign({}, filters);
    }
    case 'addSystem': {
      filters.systems.add(action.filter);
      return Object.assign({}, filters);
    }
    case 'removeSystem': {
      filters.systems.delete(action.filter);
      return Object.assign({}, filters);
    }
  }
};

export const initialFilters = {
  factions: new Set(),
  systems: new Set(),
};

export const factionsList = {
  chaos: 'Chaos',
  gsc: 'Genestealer Cults',
  imperium: 'Imperium',
  orks: 'Orks',
};

