import { useState, useReducer } from 'react';
import { useHistory } from 'react-router-dom';
import { filtersReducer, initialFilters, factionsList, systemsList } from './filtersReducer';
import { events } from './eventData';
import { StyledHeader, StyledSubheader, StyledSubheader3 } from '../styled';
import './events.css'

function Event(props) {
  const {id, title, date, src, description} = props.event;
  const history = useHistory();
  function goToEvent(id) {
    if (id) {
      history.push(`/events/${id}`);
    }
  }

  return (
    <section
      className={`event-section ${id ? 'event-record' : ''}`}
      onClick={() => goToEvent(id)}
    >
      <div className="thumb tleft">
        <div className="thumbinner" alt={title}>
          <a className="image">
            <img className="thumbimage" src={src} />
          </a>
        </div>
      </div>

      <article>
        <StyledSubheader>{title}</StyledSubheader>
        <StyledSubheader3>{date}</StyledSubheader3>
        <p>{description}</p>
      </article>
    </section>
  )
}

function EventFilter({
  filters, dispatch, showFactions, showSystems, toggleShowFactions, toggleShowSystems, results
}) {
  function filterText(set) {
    if (set.size === 0) {
      return 'All';
    }
    return Array.from(set).join(", ");
  }

  function toggleFaction(faction) {
    const actionType = filters.factions.has(faction) ? 'removeFaction' : 'addFaction';
    dispatch({type: actionType, filter: faction});
  }

  function toggleSystem(system) {
    const actionType = filters.systems.has(system) ? 'removeSystem' : 'addSystem';
    dispatch({ type: actionType, filter: system });
  }

  return (
    <div className="event-filters">
      <div className="filter-group">
        <div className="filter-summary" onClick={toggleShowFactions}>
          <span>Factions: {filterText(filters.factions)}</span>
          <i className={`arrow ${showFactions ? 'up' : 'down'}`} />
        </div>
        <ul className={showFactions ? "filter-options" : "hidden"}>
          {Object.values(factionsList).map(faction => {
            return (
              <li key={`faction-${faction}`} className="filter-option">
                <label>
                  <input
                    type="checkbox"
                    className="filter-checkbox"
                    defaultChecked={filters.factions.has(faction)}
                    onClick={() => toggleFaction(faction)}
                  />
                  {faction}
                </label>
              </li>
            );
          })}
        </ul>
      </div>

      <div className="filter-group">
        <div className="filter-summary" onClick={toggleShowSystems}>
          <span>Systems: {filterText(filters.systems)}</span>
          <i className={`arrow ${showSystems ? 'up' : 'down'}`} />
        </div>
        <ul className={showSystems ? "filter-options" : "hidden"}>
          {Object.values(systemsList).map(system => {
            return (
              <li key={`system-${system}`} className="filter-option">
                <label>
                  <input
                    type="checkbox"
                    className="filter-checkbox"
                    defaultChecked={filters.systems.has(system)}
                    onClick={() => toggleSystem(system)}
                  />
                  {system}
                </label>
              </li>
            );
          })}
        </ul>
      </div>

      <div className="filter-group">
        <div className="filter-summary">
          <span>+++Records returned: {results}+++</span>
        </div>
      </div>
    </div>
  )
}

function Events(props) {
  const [filters, dispatch] = useReducer(filtersReducer, initialFilters);
  const [showFactions, setShowFactions] = useState(false);
  const [showSystems, setShowSystems] = useState(false);
  function toggleShowFactions() {
    setShowFactions(!showFactions);
  }
  function toggleShowSystems() {
    setShowSystems(!showSystems);
  }

  let eventList = [...events].reverse();
  if (filters.factions.size) {
    eventList = eventList.filter((e) => {
      return e.factions.some((faction) => filters.factions.has(faction))
    });
  }
  if (filters.systems.size) {
    eventList = eventList.filter((e) => {
      return e.systems.some((system) => filters.systems.has(system))
    });
  }
  
  function NoEvents() {
    const factionText = filters.factions.size === 1
      ? 'that faction'
      : 'any of those factions';
    const systemText = filters.systems.size === 1
      ? 'that system'
      : 'any of those systems';
    return (
      <div style={{margin: '3em 0'}}>
        <p>No events have occurred with {factionText} in {systemText}.</p>
      </div>
    )
  }

  function EventsList({ eventList }) {
    return (
      <ul>
        {eventList.map(function (event, index) {
          return (
            <li key={`event-${index}`}>
              <Event event={event} />
            </li>
          );
        })}
      </ul>
    )
  }

  return (
    <>
      <StyledHeader>Recent Activity</StyledHeader>
      <EventFilter
        filters={filters}
        dispatch={dispatch}
        showFactions={showFactions}
        showSystems={showSystems}
        toggleShowFactions={toggleShowFactions}
        toggleShowSystems={toggleShowSystems}
        results={eventList.length}
      />
      <ul>
        {eventList.length === 0 ? <NoEvents /> : <EventsList eventList={eventList} />}
      </ul>
    </>
  );
}

export default Events;