function ImageModal({ closeModal, image }) {
  const { alt, src } = image;
  return (
    <div className="modal-layer full-screen">
      <div className="modal-backdrop">
      </div>
      <div className="modal-center full-screen" onClick={closeModal}>
        <div className="modal-body" onClick={(e) => e.stopPropagation()}>
          <div className="image-wrapper">
            <img src={src} alt={alt} />
          </div>
          <a
            href={src}
            rel="noreferrer noopener"
            target="_blank"
            role="button"
            tabindex="0"
          >
            Open in Browser
          </a>
        </div>
        </div>
    </div>
  );
}

export default ImageModal;